@use "Variables";



.panel {
  padding: Variables.$spacing;
  border-radius: Variables.$spacing * 2;
  background-color: Variables.$color2;
  display: flex;
  flex-direction: column;
  transition-duration: 0.2s;

  .titleText {
    text-align: center;
    font-size: Variables.$H2;
    font-weight: bold;
    user-select: none;
  }

  &:hover {
    transform: scale(1.01);
  }
}