$color1: #252525;
$color2: lighten($color1, 10);
$color3: lighten($color2, 10);
$color4: lighten($color3, 10);
$color5: lighten($color4, 10);

$textColor: white;

$H1: 3em;
$H2: 2.5em;
$H3: 2em;
$H4: 1.75em;
$H5: 1.2em;
$H6: 1em;

$spacing: 10px;

html {
  height: 100%;
}
body {
  background-color: $color1;
  padding: 0;
  margin: 0;
  height: 100%;
}

#root, .app {
  height: 100%;
}

.app {
  display: flex;
  flex-direction: column;

  .page {
    flex-grow: 1;
  }
}


* {
  font-family: "Century Gothic", Futura, sans-serif;
  color: $textColor;
}
